<template>
    <div>
      <Header :back="false">添加</Header>
      <ul>
        <li>
          <label for="name">书籍名称</label>
          <input type="text" v-model="book.bookName" id="name">
        </li>
        <li>
          <label for="info">书籍信息</label>
          <input type="text" v-model="book.bookInfo" id="info">
        </li>
        <li>
          <label for="price">书籍价格</label>
          <input type="text" v-model.number="book.bookPrice" id="price">
        </li>
        <li>
          <label for="pic">书籍封面</label>
          <input type="text" v-model.number="book.bookPic" id="pic">
        </li>
      </ul>
      <button @click="add">确认添加</button>
    </div>
</template>

<script>
  import Header from "../base/Header"
  import {addBook} from "../api"
    export default {
        data() {
            return {book: {}}
        },
        methods: {
          async add() {
            await addBook(this.book);
            this.$router.push("/list");
          }
        },
        computed: {},
        components: {
          Header
        },
    }
</script>

<style scoped lang="less">
  ul{
    position: absolute;
    top: 40px;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 0 5px;
    li{
      margin: 10px 0;
      label{
        display: block;
      }
      input{
        height: 25px;
        width: 100%;
        outline: none;
        border: 1px solid #e1e1e1;
      }
    }
  }
  button{
    display: block;
    margin: 5px;
    width: 100px;
    height: 30px;
    line-height: 30px;
    border: none;
    outline: none;
    background: dodgerblue;
    color: #fff;
    border-radius: 10px;
    font-size: 16px;
  }
</style>



